.nav{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    display: flex;
    z-index: 1;


    transition-timing-function: ease-in;
    transition: all0.5s;
}
.nav_black{
    background-color: #111;
}
.nav_logo{
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}
.nav_avtar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}